.signup-main--container {
    display: flex;
    flex-direction: row;
    background-color: aqua;
}
.signup--container {
    display: flex;
    flex-direction: column;
    width: 40vw;
    margin: 15px;
    padding: 20px;
}
.signup-form {
    display: flex;
    flex-direction: column;
}
h1 {
    align-self: center;
}
.instructions {
    color: rgb(102, 98, 98);
    font-size: 0.7rem;
}
label {
    padding: 10px;
    padding-left: 0px;
    margin-top: 20px;
}
input {
    width: 60%;
    padding: 10px;
}
.signup-submit-btn {
    background-color: var(--buttonGreen);
    border: none;
    height: 35px;
    width: 90px;
    margin-top: 10px;
    cursor: pointer;
    margin-bottom: 20px;
}
.signup-submit-btn:hover {
    background-color: var(--highlightedGreen);
}
.bird-cage-img {
    min-height: 100vh;
    width: 60vw;
}

.google-btn-signup {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 15px;
}
.signup-logo {
    width: 260px;
    align-self: center;
}

@media screen and (max-width: 400px) {
    .signup-main--container {
        height: 100vh;
        align-items: center;
    }
    .signup--container {
        display: flex;
        align-items: center;
        width: 80%;
    }
    .signup-form {
        width: 90%;
    }
    .signup-form input {
        width: 215px;
    }
    .signup--container h1 {
        width: 200px;
        margin-left: 50%;
        margin-right: 50%;
        text-align: center;
    }
    .google-btn-signup {
        margin-right: 20px;
        margin-top: 20px;
    }
    .signup-logo {
        width: 95px;
        position: bottom;
        margin-top: -10px;
    }
    .bird-cage-img {
        display: none;
    }
}
